.headerParent {
  display: flex;
  justify-content: start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: 95vh;
  background-color: black;
}

.headerEllipse {
  background: linear-gradient(180deg, #fb03f5 0%, #aa9cff 100%);
  filter: blur(189.415px);
  transform: rotate(50deg);
  position: absolute;
  width: 270px;
  height: 400px;
  left: 0;
  top: -30px;
}

.headerEllipseTwo {
  position: absolute;
  width: 195px;
  height: 950px;
  left: 165px;
  top: -400px;
  background: linear-gradient(261.63deg, #7000ff 4.87%, #06fff0 70%);
  filter: blur(267.94px);
  transform: rotate(30deg);
}

.spiral {
  width: 100%;
  position: absolute;
  margin-top: 5%;
  height: 80%;
}

.bigHand {
  height: 90%;
  position: absolute;
  right: 5%;
  width: 35%;
}

.titleBarParent {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100% - 20px);
  padding: 15px 10px;
  background: rgba(255, 255, 255, 0.1);
  height: calc(10% - 30px);
  z-index: 1000;
}

.logoTitleParent {
  display: flex;
  justify-content: center;
  align-items: center;
}

.headerLogo {
  width: 90px;
  height: 50px;
}

.headerTitles {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.aiverse {
  font-weight: bold;
  color: white;
  margin: 0;
  text-align: start;
}

.nfts {
  margin: 0;
  color: white;
}

.headerInternalLinks {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
}

.headerInternalLink {
  color: white;
  margin: 0 30px;
  cursor: pointer;
}

.headerExternalLink {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
}

.connectButton {
  outline: none;
  z-index: 1000;
  font-size: large;
  padding: 15px 20px;
  background: #9e64f8;
  color: white;
  border: none;
  cursor: pointer;
  margin-right: 10px;
  clip-path: polygon(
    25% 0,
    100% 0,
    100% 40%,
    100% 75%,
    75% 100%,
    39% 100%,
    0 100%,
    0% 25%
  );
}

.externalLink {
  border: none;
  outline: none;
  color: white;
  margin: 0 15px;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.headerContent {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 50%;
  padding-left: 60px;
  color: white;
  height: calc(70% - 5% - 66px);
  padding: 5%;
}

.headerAttractiveHeading {
  font-size: 70px;
  margin: 0;
  font-weight: bold;
  text-align: start;
}

.headerSubHeading {
  font-size: 50px;
  margin: 8px 0;
  text-align: start;
}

.headerHeadingContent {
  margin: 0;
  line-height: 1.5;
  text-align: start;
  font-size: large;
}

.poweredBy {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 50px;
}

.mintYourNft {
  border: 2px solid white;
  background: linear-gradient(98.35deg, #2603fb 18.95%, #dc06ff 96.51%);
  border-radius: 100px;
  padding: 20px 60px;
  color: white;
  font-size: 25px;
  cursor: pointer;
}

@media screen and (min-width: 1249px) and (max-width: 1440px) {
}

@media screen and (min-width: 1057px) and (max-width: 1248px) {
  .bigHand {
    height: 95%;
  }
}

@media screen and (min-width: 961px) and (max-width: 1056px) {
  .headerAttractiveHeading {
    font-size: 40px;
  }
  .headerSubHeading {
    font-size: 30px;
  }
  .bigHand {
    width: 42%;
    height: 90%;
    right: 0%;
  }
  .mintYourNft {
    font-size: 18px;
    padding: 20px 50px;
  }
}

@media screen and (min-width: 865px) and (max-width: 960px) {
  .headerAttractiveHeading {
    font-size: 40px;
  }
  .headerSubHeading {
    font-size: 30px;
  }
  .headerBottomContent {
    font-size: medium;
  }
}

@media screen and (min-width: 642px) and (max-width: 864px) {
  .mintYourNft {
    padding: 10px 10px;
    width: 200px;
  }
  .bigHand {
    width: 50%;
  }
  .headerInternalLinks {
    display: none;
  }
  .headerAttractiveHeading {
    font-size: 40px;
  }
  .headerSubHeading {
    font-size: 30px;
  }
}

@media screen and (max-width: 641px) {
  .headerParent {
    height: 100%;
  }

  .headerEllipse {
    width: 60%;
    height: 350px;
    top: auto;
    left: auto;
    transform: none;
  }

  .headerEllipseTwo {
    display: none;
  }

  .spiral {
    display: none;
  }

  .bigHand {
    display: none;
  }

  .headerLogo {
    width: 60px;
    height: 50px;
  }

  .headerInternalLinks {
    display: none;
  }

  .externalLink {
    display: none !important;
  }

  .headerContent {
    padding: 5%;
    width: 90%;
    height: auto;
    justify-content: center;
    align-items: center;
    background-image: url("../../assets/images/landinghand.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80% 125%;
  }

  .headerAttractiveHeading {
    text-align: center;
    margin-top: 10px;
    font-size: 30px;
  }

  .headerSubHeading {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
  }

  .headerHeadingContent {
    text-align: center;
    font-weight: bold;
  }

  .poweredBy {
    justify-content: center;
  }

  .mintYourNft {
    padding: 15px 35px;
    font-size: 15px;
  }
}

@media screen and (max-height: 641px) {
  .headerAttractiveHeading {
    font-size: 45px;
  }
  .headerSubHeading {
    font-size: 30px;
  }
}

@media screen and (min-height: 642px) and (max-height: 800px) {
  .headerAttractiveHeading {
    font-size: 45px;
  }
  .headerSubHeading {
    font-size: 30px;
  }
}
